    let activeLightbox = null;
  
  function openGallery(data, startIndex = 0) {
    if (!data) return;
  
    if (activeLightbox?.props?.isOpen) {
      try {
        activeLightbox.close();
      } catch (_) {}
    }
  
    const lightbox = new FsLightbox();
    activeLightbox = lightbox;
  
    lightbox.props.sources = data;
    lightbox.props.type = "image";
    lightbox.props.showThumbsOnMount = true;
  
    const THRESHOLD = 7;
  
    const tryAttachOverlayHandlers = () => {
      const overlay = document.querySelector(".fslightbox-container");
      if (!overlay) return false;
  
      let downX = 0,
        downY = 0,
        startedOnOverlay = false;
  
      const onPointerDown = (e) => {
        if (e.target === overlay) {
          startedOnOverlay = true;
          downX = e.clientX ?? 0;
          downY = e.clientY ?? 0;
        } else {
          startedOnOverlay = false;
        }
      };
  
      const onPointerUp = (e) => {
        if (!startedOnOverlay) return;
  
        const dx = Math.abs((e.clientX ?? 0) - downX);
        const dy = Math.abs((e.clientY ?? 0) - downY);
  
        if (dx < THRESHOLD && dy < THRESHOLD) {
          try {
            lightbox.close();
          } catch (_) {}
        }
  
        startedOnOverlay = false;
      };
  
      overlay.addEventListener("pointerdown", onPointerDown, { capture: true });
      overlay.addEventListener("pointerup", onPointerUp, { capture: true });
  
      const cleanup = () => {
        overlay.removeEventListener("pointerdown", onPointerDown, {
          capture: true,
        });
        overlay.removeEventListener("pointerup", onPointerUp, { capture: true });
        mo.disconnect();
      };
      lightbox.props.onClose = cleanup;
  
      return true;
    };
  
    const mo = new MutationObserver(() => {
      if (tryAttachOverlayHandlers()) mo.disconnect();
    });
    mo.observe(document.body, { childList: true, subtree: true });
  
    tryAttachOverlayHandlers();
  
    lightbox.open(Number.isInteger(startIndex) ? startIndex : 0);
  }
  
  /**
   *   
   * @param {Number} id
   * @param {String} source
   * @param {Number|undefined} start
   */
  async function fetchData(id, source, start) {
    const url = new URL(window.location.origin + source);
    url.searchParams.append("id", id);
    const startIndex = start ? parseInt(start, 10) : 0;
  
    try {
      const res = await fetch(url.toString(), {
        method: "GET",
        headers: { "Content-Type": "application/json" },
      });
      const data = await res.json();
      openGallery(data, startIndex);
    } catch (err) {
      console.error(err);
    }
  }
  
  function onGalleryClick(e) {
    e.preventDefault();
  
    const button = e.target.closest("a[data-gallery-id]");
    if (!button) return;
  
    const id = parseInt(button.getAttribute("data-gallery-id"), 10);
    const source = button.getAttribute("data-source");
    const start = button.getAttribute("data-start");
  
    if (id && source) {
      fetchData(id, source, start);
    }
  }
  
  document.addEventListener("DOMContentLoaded", () => {
    document.querySelectorAll("a[data-gallery-id]").forEach((el) => {
      el.addEventListener("click", onGalleryClick);
    });
  });
  
    tablesWrap();
  iframesWrap();
  
  // TABLE (wysiwyg editor) responsive
  function tablesWrap() {
    var contentTables = document.querySelectorAll(".o-content table"),
      i;
  
    for (i = 0; i < contentTables.length; ++i) {
      contentTables[i].classList.add("table");
  
      var contentTableWrap = document.createElement("div");
  
      contentTableWrap.classList.add("table-responsive");
  
      contentTables[i].parentNode.insertBefore(
        contentTableWrap,
        contentTables[i]
      );
  
      contentTableWrap.appendChild(contentTables[i]);
    }
  }
  
  // IFRAME youtube/google (wysiwyg editor) responsive
  function iframesWrap() {
    var contentIframes = document.querySelectorAll(".o-content iframe"),
      i;
  
    for (i = 0; i < contentIframes.length; ++i) {
      contentIframes[i].removeAttribute("height");
      contentIframes[i].removeAttribute("width");
  
      var iframeWrap = document.createElement("div");
      iframeWrap.classList.add("ratio");
      iframeWrap.classList.add("ratio-16x9");
  
      contentIframes[i].parentNode.insertBefore(iframeWrap, contentIframes[i]);
  
      iframeWrap.appendChild(contentIframes[i]);
    }
  }
  
